import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { PsKeyinInputComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-field-keyin/ps-keyin-input/ps-keyin-input.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsActionIcon } from '../../../../commonSRC/psServices/models/ps-common-interface';
import { PsNavigatorService } from '../../../../commonSRC/psServices/navigator/ps-navigator.service';
import { IOptionsPsInputAccountNumberExposed } from './ps-input-account-number.component.interface';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';


@Component({
  selector: 'ps-input-account-number',
  templateUrl: './ps-input-account-number.component.html',
  styleUrls: ['./ps-input-account-number.component.scss'],
})
export class PsInputAccountNumberComponent extends PsKeyinInputComponent implements OnInit {
  @Input() options: IOptionsPsInputAccountNumberExposed;
  @Output() getScannedQr = new EventEmitter();
  actionOption: IOptionsPsActionIcon = {};
  isInReadOnlyScreen: boolean;
  // public format: any = 2;
  // public accountLength: any = 8;

  constructor(commonP: PsCommonService, loggerP: LoggerService, private omniPull: OmniPullService, public nav: PsNavigatorService) {
    super(commonP, loggerP);
    //   this.omniPull.getParamValOf('AccountsInputFormat', 'AccountAddRefLength').then((result) => {
    //     if (result.AccountsInputFormat) {
    //       this.format = result.AccountsInputFormat;
    //     }
    //     if (result.AccountAddRefLength) {
    //       this.accountLength = result.AccountAddRefLength;
    //     }
    //   }).catch((error) => { });
    if (this.custMode)
      this.options.showScannerIcon = true;
  }


  ngOnInit() {
    // don't remove ngOnInit() it's needed in 
    let operCustomization = this.common.returnOperCustomization();
    this.isInReadOnlyScreen = operCustomization.get(ConstantCommon.FORM_READONLY)?.IS_READONLY == 1 ? true : false;
    if (CommonUtils.isNotNull(this.options)) {
      this.options.hideValueOnError = true;
    }
    this.actionOption = {
      iconName: 'qr_code_scanner',
      allowCust: true,
      group: this.options.group
    }

  }

  onChangeEvent(val) {
    this.onPsChange.emit(val);
  }
  getScannedData(buttonClicked: boolean) {
    if (!this.custMode) {
      // set force trigger true so that once account is scanned the validation will be triggered to apply the needed validation on account change
      if (this.options.forceTriggerChange == false)
        this.options.forceTriggerChange = true;
      // this.commonProv.presentLoading();
      this.commonProv.scanQRCode().then(result => {
        if (result) {
          result = JSON.parse(result);
          this.commonProv.setValInsideNestedObj(this.options.fcName, result.AccountRef?.split('-')[1], this.options.requestObject ? this.options.requestObject : {});
          const data = { ...result, accountNumber: result.AccountRef?.split('-')[1] }
          this.getScannedQr.emit([data, buttonClicked]);
        }
        // this.commonProv.dismissLoading();
      }).catch(error => {
        // this.commonProv.dismissLoading();
        this.logger.error('something went wrong', error);
        this.nav.navigateBackward();
      });
    }
  }
}